/** @jsx React.createElement */

import React from 'react';
import {BEMBlock} from "../utils";
import TJInput from '../tj-input/tj-input.jsx';

const
	CLASS_NAME = 'tj-select-default',
	BEM = new BEMBlock(CLASS_NAME);

export class TJSelectDefault extends React.Component {

	state = {
        expanded : false,
    };

    componentDidMount() {
        window.addEventListener('click', this.clickOutsideOfSelect);
    }


    clickOutsideOfSelect = (event) => {
        if (!this.selectContainerRef.contains(event.target)) {
            this.setState({ expanded: false });
        }
    };

    setSelectContainerRef = (element) => {
        this.selectContainerRef = element
    };

	onSelect = (option) => () => {
        this.setState({ expanded : false });
        this.props.onChange(option);
    };

	onToggle = () => {
		this.setState({ expanded : !this.state.expanded });
	};

	render() {
        return (
            <TJSelectContainer
                className={BEM.exec()}
                containerRef={this.setSelectContainerRef}
                expanded={this.state.expanded}
                controller={
                    <TJSelectControllerButton
                        className={BEM.el('expand-button').exec()}
                        onToggle={this.onToggle}
                        text={this.props.selected.name}
                        expanded={this.state.expanded}
                    />
                }
            >
                <TJSelectOptionList
                    className={BEM.el('option-list').exec()}
                    expanded={this.state.expanded}
                >
                    { this.props.options.map(option => {
                        return <TJSelectOption
                            key={option.name}
                            className={BEM.el('option').exec()}
                            selected={this.props.selected.name === option.name}
                            onSelect={this.onSelect(option)}
                            name={option.name}
                        />
                    })}
                </TJSelectOptionList>
            </TJSelectContainer>
        )
	}

    componentWillUnmount() {
        window.removeEventListener('click', this.clickOutsideOfSelect)
    }

}


export class TJSelectFiltered extends React.Component {

    state = {
        expanded : false,
        filter : ''
    };

    componentDidMount() {
        window.addEventListener('click', this.clickOutsideOfSelect);
    }

    clickOutsideOfSelect = (event) => {
        if (!this.selectContainerRef.contains(event.target)) {
            this.setState({ expanded: false });
        }
    };

    setSelectContainerRef = (element) => {
        this.selectContainerRef = element
    };

    onSelect = (option) => () => {
        console.log(option)
        this.setState({
            expanded : false,
            filter : '',
        });

        this.props.onChange(option);
    };

    onInputFocus = () => {
        this.setState({ expanded : true });
    };

    onFilterChange = (newFilter) => {
        this.setState({ filter : newFilter });
    };

    render() {

        const {
            expanded,
            filter
        } = this.state;

        const {
            selected,
            options,
            filterFunction
        } = this.props;

        return (
            <TJSelectContainer
                className={BEM.exec()}
                containerRef={this.setSelectContainerRef}
                expanded={expanded}
                controller={
                    <TJSelectControllerFilterInput
                        className={BEM.el('filter-button').exec()}
                        onFilterChange={this.onFilterChange}
                        onFocus={this.onInputFocus}
                        filter={filter}
                        selected={selected.name}
                    />
                }
            >
                <TJSelectOptionList
                    className={BEM
                        .el('option-list')
                        .mod('expanded', expanded)
                        .exec()}
                >
                    { options
                        .filter(option => {
                            return filterFunction
                                ? filterFunction(option, filter)
                                : option.name.indexOf(filter) !== -1
                        })
                        .map(option => {
                            return <TJSelectOption
                                key={option.name}
                                className={BEM.el('option').exec()}
                                selected={this.props.selected.name === option.name}
                                onSelect={this.onSelect(option)}
                                name={option.name}
                            />
                        })
                    }
                </TJSelectOptionList>
            </TJSelectContainer>
        )
    }

    componentWillUnmount() {
        window.removeEventListener('click', this.clickOutsideOfSelect)
    }

}

export function TJSelectContainer({ controller, className, containerRef, children, expanded }) {

    return (
        <div
            ref={containerRef}
            className={className}
        >
            { controller }
            { expanded
                ? children
                : ''
            }
        </div>
    )
}


export function TJSelectControllerButton({ className, text, onToggle }) {
    return (
        <button
            className={className}
            onClick={onToggle}
        >
            { text }
        </button>
    )
}


export function TJSelectControllerFilterInput({ className, filter, onFocus, onFilterChange, selected }) {
    return (
        <TJInput
            type='text'
            value={filter}
            className={className}
            placeholder={selected}
            onFocus={onFocus}
            onInput={onFilterChange}
        />
    )
}


export function TJSelectOptionList({ className, children }) {
	return (
		<div
            className={className}
		>
            { children }
		</div>
	)
}

export function TJSelectOption({ selected, onSelect, className, name}) {
	return (
		<button
			className={className + (selected ? ` ${className}--selected` : '')}
			onClick={onSelect}
		>
			{ name }
		</button>
	)
}


