// TODO: Убрать флоу, добавить конфиг
export class BEMBlock{

	_block : string;
	_value : string;
	_element : string;

	constructor(name : string) {
		this._block = name;
		this._value = name;
		this._element = name;
	}

	exec() {
		let result = this._value;
		this._value = this._block;
		this._element = this._block;
		return result
	};

	el(elName: string) {
		this._element = this._block + '__' + elName;
		this._value = this._element;
		return this;
	};

	mod(mod: string, value: string | boolean = true) {
		if (typeof value === 'string') {
			this._value += ` ${this._element}--${mod}--${value}`;
		} else if (value) {
			this._value += ` ${this._element}--${mod}`;
		}
		return this;
	};
}
